/*------ header start ------*/
.header {
  margin: 10px 0 0 0;
  .header-back {
    position: relative;
    overflow: hidden;
    img {
      transition: all .5s ease-in-out;
    }
    &:hover {
      img {
        transform: scale(1.02);
      }
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      display: none;
      .owl-prev, .owl-next {
        outline: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: 30.348px;
        color: rgba(255, 255, 255, 0.702);
        transition: all .5s ease-in-out;
        &:hover {
          color: #b39861;
        }
      }
      .owl-prev {
        right: 30px;
      }
      .owl-next {
        left: 30px;
      }
    }
    .owl-dots {
      width: max-content;
      position: absolute;
      left: 50%;
      bottom: 40px;
      transform: translateX(-50%);
      .owl-dot {
        width: 9px;
        height: 9px;
        background-color: rgba(29, 34, 34, 0.7);
        border-radius: 50%;
        outline: none;
        margin: 0 4px;
        &.active {
          background-color: rgba(255,255,255,1.0);
        }
      }
    }
  }
}
/*------ header end ------*/



