/*--- card start ---*/
.card {
  border-style: solid;
  border-width: 1px;
  border-color: rgb(235, 235, 235);
  background-color: rgb(255, 255, 255);
  text-align: center;
  .card-img {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 220px;
    img {
      transition: all .5s ease-in-out;
    }
    &:hover {
      img {
        transform: scale(0.940);
      }
    }
  }
  .card-body {
    margin: 0 0 0 0;
    .card-title {
      font-family: ar-reg;
      font-size: 18px;
      color: rgb(0, 0, 0);
      min-height: 55px;
      display: block;
    }
    .card-price {
      margin: 0 0 20px 0;
      .new {
        font-family: ar-bold;
        font-size: 18px;
        color: rgb(0, 0, 0);
      }
    }
    .card-amount {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0 auto 7px auto;
      width: 90%;
      height: 36px;
      border-radius: 50px;
      border-style: solid;
      border-width: 1px;
      border-color: rgb(235, 235, 235);
      padding: 0 9px;
      button {
        outline: none;
        border: none;
        background-color: transparent;
        padding: 0;
        font-size: 18px;
        color: rgb(177, 177, 177);
        transition: all .5s ease-in-out;
        &:hover {
          color: #000;
        }
      }
      span {
        font-family: ar-md;
        font-size: 16px;
        color: rgb(0, 0, 0);
      }
      
    }
  }
  .add-to-card {
    outline: none;
    border: none;
    padding: 0;
    margin: 0 auto 20px auto;
    width: 90%;
    height: 32px;
    border-radius: 50px;
    background-color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
    font-family: ar-md;
    font-size: 14px;
    transition: all .5s ease-in-out;
    span {
      font-size: 16px;
      margin: 0 0 0 5px;
    }
    &:hover {
      background-color: rgb(255, 230, 0);
      color: #000;
    }
  }
}
