/*------ responsive ------*/
@media (min-width: 1600px) {
  .container {
    width: 1400px;
  }
  .card .card-body .card-title {
    padding: 0 12px;
  }
}
@media (max-width: 992px) {
  .upper-bar {
    display: none;
  }
  .nav-bar {
    padding: 5px 0;
  }
  .nav-bar .icons .user-icon,
  .nav-bar .icons .fav-icon {
    display: none;
  }
  .nav-bar .icons .card-icon span:nth-of-type(1) {
    display: none;
  }
  .nav-bar .icons .card-icon {
    .card-num-icon {
      width: 18px;
      height: 18px;
      font-size: 14px;
      top: -9px;
      left: -9px;
      padding: 4px 0 0 0;
    }
  }
  .nav-bar .icons .menu-icon,
  .nav-bar .icons .search-icon {
    button {
      outline: none;
      border: none;
      background-color: transparent;
      padding: 0;
    }
    display: block;
    display: flex;
    margin: 0 0 0 25px;
    color: rgb(15, 15, 15);
    font-size: 22px;
    transition: all .5s ease-in-out;
    cursor: pointer;
    &:hover {
      color: rgb(255, 230, 0);
    }
  }
  .nav-bar .delivery {
    display: none;
  }
  .nav-bar .option {
    display: none;
  }
  .nav-list {
    display: none;
  }
  .nav-bar .icons .zx-container {
    margin: unset;
  }
  .nav-bar .logo {
    width: 120px;
  }
  .nav-bar .search {
    opacity: 0;
    visibility: hidden;
    margin: unset;
    width: 80%;
    position: absolute;
    top: 130px;
    left: 50%;
    transform: translateX(-50%);
    transition: all .5s ease-in-out;
    z-index: 9;
    &.show {
      opacity: 1;
      visibility: visible;
      top: 95px;
    }
    input {
      width: 100%;
      border: 1px solid #000;
    }
  }
  .fixed-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 999;
    .menu {
      width: 280px;
      height: 100%;
      position: absolute;
      top: 0;
      right: -280px;
      background-color: #fff;
      padding: 20px 20px;
      overflow-y: auto;
      transition: all .5s ease-in-out;
      &.show {
        right: 0;
      }
    }
    .menu-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .close {
        opacity: 1;
        color: #444;
        font-size: 16px;
      }
    }
    .main-list {
      margin: 30px 0px;
      transform: translateX(50%);
      transition: all .5s ease-in-out;
      .slide {
        cursor: pointer;
        position: relative;
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: rgb(15, 15, 15);
          position: absolute;
          top: 8px;
          left: 10px;
          transition: all .5s ease-in-out;
        }
        &.active {
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-md;
        font-size: 16px;
        color: #444;
        margin: 7px 0;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
      &.show {
        transform: translateX(0%);
      }
    }
    .sub-list {
      padding: 5px 10px;
      display: none;
      .slide-two {
        cursor: pointer;
        position: relative;
        
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: rgb(15, 15, 15);
          position: absolute;
          top: 9px;
          left: 15px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #b39861;
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-reg;
        font-size: 16px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
    .sub-list-copy {
      padding: 5px 15px;
      display: none;
      li {
        font-family: ar-reg;
        font-size: 15px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
  }
  .fixed-menu .menu-header .lang-switcher {
    margin: 0;
    .switch {
      left: 10px;
    }
  }
  .header {
    margin: 10px 0 30px 0;
    .owl-carousel {
      position: relative;
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: 10px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: rgba(0,0,0,0.7);
          border-radius: 50%;
          outline: none;
          margin: 0 5px;
          &.active {
            background-color: #fff;
          }
        }
      }
    }
  }
  .header .header-back .img-title {
    justify-content: center;
  }
  .header .header-back .img-title a {
    display: none;
  }
  .header .owl-carousel .owl-nav {
    display: none;
  }
  .category .zx-container {
    flex-direction: column;
  }
  .category {
    margin: 0px 0;
    .cat {
      width: max-content;
      height: unset;
      margin: 20px auto;
    }
  }
  .sp-offers {
    .sp-offers-header {
      margin: 35px 0 25px 0;
    }
    .card {
      margin: 0 auto;
      .time {
        padding: 8px 0 0 0;
      }
    }
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: -25px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: rgba(0,0,0,0.7);
          border-radius: 50%;
          outline: none;
          margin: 0 5px;
          &.active {
            background-color: rgb(255, 230, 0);
          }
        }
      }
    }
  }
  .panners .zx-container {
    flex-direction: column;
  }
  .panners .panner-img {
    margin: 5px 0;
  }
  .most-sales {
    .most-sales-header {
      margin: 35px 0 20px 0;
    }
    .card {
      margin: 0 auto;
    }
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: -25px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: rgba(0,0,0,0.7);
          border-radius: 50%;
          outline: none;
          margin: 0 5px;
          &.active {
            background-color: rgb(255, 230, 0);
          }
        }
      }
    }
  }
  .servs .zx-container {
    height: auto;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10px 0;
  }
  .serv {
    padding: 10px 0;
    &:nth-of-type(2) {
      border: none !important;
    }
  }
  .footer {
    padding: 40px 10px;
    .footer-logo {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .footer-text {
      text-align: center;
      padding: 0 10px;
      margin: 0 0 40px 0;
    }
    .list-header {
      margin: 15px 0;
      position: relative;
      &::after {
        content: "";
        font-family: "Linearicons-Free";
        font-size: 12px;
        color: #000;
        position: absolute;
        top: 50%;
        left: 0px;
        transform: translateY(-50%);
        transition: all .5s ease-in-out;
      }
      &.active {
        &::after {
          content: "";
        }
      }
    }
    ul {
      display: none;
      padding: 0 0 5px 0;
      margin: unset;
      li {
        margin: unset;
        padding: 0 0 10px 0;
      }
    }
    .sub-header {
      text-align: center;
      margin: 50px 0 20px 0;
    }
    .footer-sub p {
      text-align: center;
    }
    .subscribe button {
      background-color: #000;
      border: 2px solid #000;
      color: #fff;
      left: 10px;
    }
    .footer-icons {
      margin: 30px 0 0 0;
      justify-content: center;
    }
    .copy-container {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
    .copyrights {
      order: 2;
      margin: 10px 0 0 0;
    }
    .payment {
      order: 1;
      padding: 30px 0 20px 0;
    }
  }
}



@media (max-width: 480px) {
  .nav-bar .search {
    width: 94%;
  }
  .header {
    margin: 5px 0 0 0;
  }
  .category .cat {
    margin: 25px auto;
  }
  .category .cat .cat-header {
    font-size: 20px;
    margin: 20px 0 0 0;
  }
  .most-sales .most-sales-header {
    font-size: 22px;
  }
  .sp-offers .sp-offers-header {
    font-size: 22px;
  }
  .servs {
    padding: 0 0;
    .serv {
      width: unset;
      padding: 20px 0;
    }
  }
  .footer .payment {
    padding: 50px 0 30px 0;
  }
  .footer .copyrights {
    flex-direction: column;
    span {
      text-align: center;
      line-height: 1.8;
      &:nth-of-type(2) {
        display: none;
      }
    }
  }
}


/*------ direction ------*/
html[dir="ltr"] {
  .nav-bar .delivery {
    right: unset;
    left: 0;
  }
  .nav-bar .delivery img {
    margin: 0 10px 0 0;
  }
  .nav-bar .option {
    left: unset;
    right: 0;
  }
  .nav-bar .option .our-num span {
    padding: 0 0 0 5px;
  }
  .lang-switcher {
    margin: 0 15px 0 30px;
    &::after {
      left: unset;
      right: -15px;
    }
  }
  .lang-switcher img {
    margin: 0 7px 0 0;
  }
  .nav-bar .search button {
    right: unset;
    left: 20px;
  }
  .nav-bar .icons .user-icon {
    padding: 0 30px 0 0;
    margin: 0 30px 0 0;
    border-left: unset;
    border-right: 2px solid #cccccc;
  }
  .nav-bar .icons .card-icon .card-num-icon {
    right: unset;
    left: 15px;
  }
  .nav-list .list li {
    border-left: unset;
    border-right: 1px solid #485454;
    &:last-child {
      border: none;
    }
  }
  .card .add-to-card span {
    margin: 0 5px 0 0;
  }
  .footer .subscribe button {
    left: unset;
    right: 20px;
  }
  .footer .copyrights img {
    width: 60px;
  }


  @media (max-width: 992px) {
    .nav-bar .icons .menu-icon,
    .nav-bar .icons .search-icon {
      margin: 0 25px 0 0;
    }
    .fixed-menu .menu {
      left: -280px;
      right: unset;
      &.show {
        left: 0;
        right: unset;
      }
    }
    .fixed-menu .menu-header .lang-switcher .switch {
      left: 65px;
    }
    .footer .list-header::after {
      left: unset;
      right: 0;
    }
    .footer .list-header.active {
      &::after {
        content: "";
      }
    }
  }
}